import { getType } from 'typesafe-actions';
import { fetchResourceFailure, fetchResourceRequest, fetchResourceSuccess } from './resourceActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function resourceReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchResourceRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.resourceId, 1 /* inProgress */) });
        case getType(fetchResourceSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.resourceId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.resourceId, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.resourceId, null) });
        case getType(fetchResourceFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.resourceId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.resourceId, action.payload) });
        default:
            return state;
    }
}
