import axios from 'axios';
export const ResourceTypePaths = {
    ["account" /* account */]: 'resource-types/gcp-accounts/',
    ["project" /* project */]: 'resource-types/gcp-projects/',
    ["region" /* region */]: 'resource-types/gcp-regions/',
    ["service" /* service */]: 'resource-types/gcp-services/',
};
export function runResource(resourceType, query) {
    const insights = window.insights;
    const path = ResourceTypePaths[resourceType];
    if (insights && insights.chrome && insights.chrome.auth && insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
