import { ibmCostOverviewStateKey } from './ibmCostOverviewCommon';
export const selectIbmCostOverviewState = (state) => state[ibmCostOverviewStateKey];
export const selectWidgets = (state) => selectIbmCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectIbmCostOverviewState(state).currentWidgets;
