import { ibmHistoricalDataStateKey } from './ibmHistoricalDataCommon';
export const selectIbmHistoricalDataState = (state) => state[ibmHistoricalDataStateKey];
export const selectWidgets = (state) => selectIbmHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectIbmHistoricalDataState(state).currentWidgets;
