import { getQueryForWidget, getQueryForWidgetTabs, ocpInfrastructureDashboardDefaultFilters, ocpInfrastructureDashboardStateKey, ocpInfrastructureDashboardTabFilters, } from './ocpInfrastructureDashboardCommon';
export const selectOcpInfrastructureDashboardState = (state) => state[ocpInfrastructureDashboardStateKey];
export const selectWidgets = (state) => selectOcpInfrastructureDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpInfrastructureDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, ocpInfrastructureDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ocpInfrastructureDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
